import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpService } from 'src/app/services/http.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

 registerForm: FormGroup;
    submitted = false;

    constructor(private formBuilder: FormBuilder,public service:HttpService,private toastr: ToastrService,) { }

    ngOnInit() {
        this.registerForm = this.formBuilder.group({
            name: ['', Validators.required],
            email: ['', [Validators.required, Validators.pattern(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,6})$/)]],
            message: ['', Validators.required],
           
        });
    }

    // convenience getter for easy access to form fields
    get f() { return this.registerForm.controls; }

    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }
        var user_id = '0';
        if(localStorage.getItem('userId') != null){
            user_id=localStorage.getItem('userId');
        }
        this.registerForm.value.msg=this.registerForm.value.message
        this.service.post('/user/addContactUs/'+user_id,this.registerForm.value)

      .subscribe(res => {
                        this.toastr.success(res['msg']);
                        this.onReset()

      })
    }

    onReset() {
        this.submitted = false;
        this.registerForm.reset();
    }
}
